import React from 'react'
import {
    Routes,
    Route,
    BrowserRouter,
} from 'react-router-dom';
import MainPage from './MainPage';
import Perfil from './Perfil';

export default function ContenidoApp(props) {
    return (

        <Routes>
            <Route path='/' element={<MainPage auth={props.auth} user={props.user} />}></Route>
            <Route path='/pelfil' element={<Perfil auth={props.auth} user={props.user} />}></Route>
        </Routes>
    )
}